@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
/* @format */
/* navbar.css -- stylesheets for navigation bar */

.navbar {
  z-index: 1;
  position: -webkit-sticky;
  position: sticky;
  display: -webkit-flex;
  display: flex;
  top: 0px;
  background-color: var(--navbar-bg-color);
  color: var(--navbar-txt-color);
  padding-top: 1em;
  padding-bottom: 1em;
  padding-left: 1em;
  opacity: 0.95;
}

#smallLogoDiv {
  padding: 1px;
  margin-right: 2em;
  margin-left: 5px;
  margin-top: 3px;
  background-color: var(--navbar-txt-color);
}
#smallLogo {
  position: relative;
  vertical-align: bottom;
  padding-top: 3px;
  padding-left: 3px;
  padding-right: 3px;
  padding-bottom: 3px;
}

#bigLogo {
  padding-left: 2em;
}

.navbutton {
  background-color: var(--navbar-header-color);
  color: var(--navbar-txt-color);
  border-style: none;
  vertical-align: top;
  margin-right: 2em;
}

.navbutton:hover {
  color: var(--main-accent-color);
  cursor: pointer;
}

/* @format */
/* displayDrinks.css -- displaying drink names and data on the screen*/

.drinkWrapper {
  display: grid;
  grid-template-columns: 6% 94%;
  grid-template-rows: auto;
  color: var(--main-accent-color);
  font-weight: bold;
  padding-bottom: 2em;
}
.glass {
}
#drinkName {
  font-size: 150%;
}
#ingreds {
  font-size: 80%;
  color: var(--main-text-color);
  font-weight: normal;
  padding-top: 0.5em;
  padding-left: 1em;
  font-style: italic;
}
.metrics {
  color: var(--main-text-color);
  font-weight: bold;
  font-size: 80%;
  padding-left: 1em;
}

.recipe {
  padding: 0;
  grid-column: 2 / 3;
  color: var(--main-text-color);
  font-weight: normal;
  margin: 0;
  margin-left: 0.5em;
  font-size: 90%;
  line-height: 0.5;
  cursor: pointer;
}
#build {
  line-height: 1;
}
.hideshow {
  margin: 0;
  text-indent: 3em;
  padding-top: -2em;
  font-size: 75%;
  font-style: normal;
}

.nameData {
  padding-right: 1em;
}

/* @format */
/* spinner.css -- stylesheets for loading spinner */

#flute1 {
  margin: 0;
  padding: 0;
  position: relative;
  top: 1em;
  bottom: 1em;
  left: 1em;
  right: 1em;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: clinkR;
          animation-name: clinkR;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
#flute2 {
  margin: 0;
  padding: 0;
  position: relative;
  top: 1em;
  bottom: 1em;
  left: 0em;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: clinkL;
          animation-name: clinkL;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes clinkR {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  15% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
  }
}
@keyframes clinkR {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  15% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
  }
}
@-webkit-keyframes clinkL {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  15% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
}
@keyframes clinkL {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  15% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
}
#loading {
  position: absolute;
  left: 7em;
}

/* @format */

#welcome {
  display: grid;
  grid-template-rows: auto auto;
}

#bigLogo {
  padding-top: 1em;
  padding-bottom: 1em;
  grid-row: 1 / 2;
}

#buttonWrapper {
}

#welcome button {
  font-family: var(--primary-fontfam);
  color: var(--main-text-color);
  height: 10em;
  width: 15em;
  font-size: 1em;
  margin: 0.5em;
  background-color: var(--main-bg-color);
  border-color: var(--main-accent-color);
  border-radius: 10px;
  transition-duration: 0.2s;
}

#welcomeIng {
  margin-left: 1em;
}

#welcome button:hover {
  color: var(--main-bg-color);
  background-color: var(--main-accent-color);
  cursor: pointer;
}

